import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";
import LoginTest from "@/views/LoginTest.vue";
import NavView from "@/views/NavView.vue";
import HomeView from "@/views/HomeView.vue";
import BookView from "@/views/BookView.vue";
import BorrowView from "@/views/BorrowView.vue";
import ReturnView from "@/views/ReturnView.vue";
import OvertimeView from "@/views/OvertimeView.vue";
import UserView from "@/views/UserView.vue";
import SystemView from "@/views/SystemView.vue";
import LoginViewUser from "@/views/LoginViewUser.vue";
import NavViewUser from "@/views/NavViewUser.vue";
import BookViewUser from "@/views/BookViewUser.vue";
import UserViewUser from "@/views/UserViewUser.vue";
const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "",
    component: LoginTest,
    meta: { show: false },
  },
  {
    path: "/LoginTest",
    name: "LoginTest",
    component: LoginTest,
    meta: { show: false },
  },
  {
    path: "/loginUser",
    name: "",
    component: LoginViewUser,
    meta: { show: false },
  },
  {
    path: "/nav",
    name: "",
    component: NavView,
    redirect: "/home",
    meta: { show: false },
    children: [
      {
        path: "/home",
        name: "",
        meta: { show: true },
        components: { home: HomeView },
      },
    ],
  },
  {
    path: "/nav",
    name: "",
    component: NavView,
    meta: { show: true },
    children: [
      {
        path: "/book",
        name: "",
        meta: { show: true },
        components: { book: BookView },
      },
      {
        path: "/borrow",
        name: "",
        meta: { show: true },
        components: { borrow: BorrowView },
      },
      {
        path: "/return",
        name: "",
        meta: { show: true },
        components: { return: ReturnView },
      },
      {
        path: "/overtime",
        name: "",
        meta: { show: true },
        components: { overtime: OvertimeView },
      },
    ],
  },
  {
    path: "/nav",
    name: "",
    component: NavView,
    meta: { show: true },
    children: [
      {
        path: "/user",
        name: "",
        meta: { show: true },
        components: { user: UserView },
      },
    ],
  },
  {
    path: "/nav",
    name: "",
    component: NavView,
    meta: { show: true },
    children: [
      {
        path: "/system",
        name: "",
        meta: { show: true },
        components: { system: SystemView },
      },
    ],
  },
  {
    path: "/navUser",
    name: "User",
    component: NavViewUser,
    redirect: "/bookUser",
    meta: { show: false },
    children: [
      {
        path: "/bookUser",
        name: "",
        component: BookViewUser,
        meta: { show: true },
      },
      {
        path: "/userUser",
        name: "",
        component: UserViewUser,
        meta: { show: true },
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

export default router;
